import _ from 'lodash';
import api from 'utils/api'
function sortBy(arr) { //排序初始化
  return _.sortBy(arr, (item) => {
    return item.sortId
  })
}

function getRoles(callback) { //得到用户列表
  api.get(`/role`).then(res => {
    if (callback) callback(res.data)
    return res.data;
  })
  .catch(err => {
    console.log(err)
  });
  return [];
}

function getCompany(callback) { //得到企业列表
  api.get(`/company?page=0&size=1000`).then(res => {
    if (callback) callback(res.data)
    return res.data;
  })
  .catch(err => {
    console.log(err)
  });
  return [];
}

function getGoods(id, callback) { //得到企业列表
  if(!id) return;
  api.get(`/goods?companyId=${id}&page=0&size=1000`).then(res => {
    if (callback) callback(res.data)
    return res.data;
  })
  .catch(err => {
    console.log(err)
  });
  return [];
}

